<template>
  <div id="user" class="content">
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="useredit" v-loading="loading">
      <el-form ref="form" :model="form" label-width="120px">
        <el-form-item label="">
          <el-input v-model="form.username" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.realName" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="form.gender" :disabled="true">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.phoneNumber"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.idcardNumber" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="()">
          <el-input v-model="form.spreadCode" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="()">
          <el-input v-model="form.spreadCode2" placeholder="" class="inline-input"></el-input> 1~9
        </el-form-item>
        <el-form-item label="">
          <el-switch on-color="#13ce66" off-color="#f7ba2a" on-text="" off-text="" v-model="form.isDisabled"></el-switch>
        </el-form-item>
        <el-form-item>
          <el-button @click="onCancel"></el-button>
          <el-button type="primary" icon="circle-check" @click="onSubmit" style="width: 150px;"> </el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  import * as SITEURL from '@/store/app-urls'
  export default {
    name: 'user',
    data () {
      return {
        form: {
        },
        loading: false
      }
    },
    created: function () {
      this.form = this.$store.state.member
      this.form.isDisabled = this.form.isDisabled === 0
    },
    methods: {
      onSubmit () {
        if (!/^\d{0}|\d{1,9}$/.test(this.form.spreadCode2)) {
          this.$message.warning('1~9')
          return
        }
        if (!/^1\d{10}$/.test(this.form.phoneNumber)) {
          this.$message.warning('')
          return
        }
        this.loading = true
        this.form.isDisabled = this.form.isDisabled ? 0 : 1
        this.$axios.post(SITEURL.MEMBER_EDIT_URL, this.form).then(response => {
          this.loading = false
          if (response.data.success) {
            this.$message.success('' + response.data.message)
          } else {
            this.$message.error('' + response.data.message)
          }
          this.$router.go(-1)
        }).catch(error => {
          this.loading = false
          console.log(error)
        })
      },
      onCancel () {
        this.$router.go(-1)
      },
      handleCurrentChange (val) {
        this.currentPage = val
        console.log(`: ${val}`)
      }
    }
  }
</script>

<style>
  .inline-input {
    width: 72%;
  }
  .useredit {
    display: block;
    width: 40%;
    border: 1px solid #eaeefb;
    border-radius: 4px;
    transition: .2s;
    padding: 24px;
  }
</style>



