<template>
  <div id="withdraw-list" class="content">
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="search" @keyup.enter="onSearch">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="">
          <el-input class="inline-input" v-model="formInline.search" placeholder="||"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSearch" icon="search"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="bankcardlist">
      <el-table :data="tableData" stripe border style="width: 100%" :height="$store.state.tableHeight" type="selection" v-loading="loading">
        <el-table-column prop="createDate" label="" width="120"></el-table-column>
        <el-table-column prop="applyStatusLabel" label="" width="70"></el-table-column>
        <el-table-column prop="realName" label="" width="80"></el-table-column>
        <el-table-column prop="companyName" label="" width="200"></el-table-column>
        <el-table-column prop="bankName" label=""></el-table-column>
        <el-table-column prop="bankDetail" label=""></el-table-column>
        <el-table-column prop="cardNumber" label="" width="180"></el-table-column>
        <el-table-column prop="phoneNumber" label="" width="170"></el-table-column>
        <el-table-column prop="licenseImg" label="" width="140">
          <template scope="scope">
            <img v-lazy="scope.row.licenseImg" class="cover-img" @click="handlePictureCardPreview" />
          </template>
        </el-table-column>
        <el-table-column  prop="remark" label="" >
        </el-table-column>
        <el-table-column label="" fixed="right" width="150">
          <template scope="scope">
            <el-button size="small" type="success" @click="handleAudit(scope.$index, scope.row, 1)" v-if="scope.row.applyStatus === 0"></el-button>
            <el-button size="small" type="danger" @click="handleAudit(scope.$index, scope.row, 2)" v-if="scope.row.applyStatus === 0"></el-button>
          </template>
        </el-table-column>      
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
       :current-page="currentPage"
       :page-size="pageSize"
       layout="total, prev, pager, next"
       :total="total">
      </el-pagination>
    </div>
    <el-dialog v-model="dialogVisible" size="tiny">
      <img width="100%" height="100%" :src="dialogImageUrl" alt="">
    </el-dialog>
  </div>
</template>

<script>
  import * as SITEURL from '@/store/app-urls'
  export default {
    name: 'user',
    data () {
      return {
        formInline: {
          search: ''
        },
        tableData: [],
        currentPage: 1,
        pageSize: 1,
        total: 1,
        fromDate: '',
        endDate: '',
        dialogImageUrl: '',
        dialogFormVisible: false,
        form: { cause: '' },
        formLabelWidth: '80px',
        loading: false,
        dialogVisible: false,
        rejectTxt: '',
        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick (picker) {
              picker.$emit('pick', new Date())
            }
          }, {
            text: '',
            onClick (picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', date)
            }
          }, {
            text: '',
            onClick (picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', date)
            }
          }]
        },
        options: [{
          value: 'waiting',
          label: ''
        }, {
          value: 'handled',
          label: ''
        }],
        value: ''
      }
    },
    created: function () {
      this.loading = true
      this.getBankInfo()
    },
    methods: {
      getBankInfo (page = 1) {
        // GET /someUrl
        this.$axios.get(SITEURL.BANKCARD_APPLY_LIST + '?page=' + page).then(response => {
          // get body data
          this.loading = false
          this.tableData = response.data.data.data
          this.currentPage = response.data.data.current_page
          this.pageSize = response.data.data.per_page
          this.total = response.data.data.total
        }).catch(error => {
          console.log(error)
        })
      },
      onSearch () {
        var text = this.formInline.search.trim()
        this.loading = true
        if (!this.formInline.search) {
          this.getBankInfo()
        } else {
          this.$axios.get(SITEURL.BANKCARD_APPLY_SEARCH + '?text=' + text).then(response => {
            // get body data
            this.loading = false
            this.tableData = response.data.data
            this.currentPage = 1
            this.total = response.data.data.length
          }).catch(error => {
            console.log(error)
          })
        }
      },
      // 
      handleAudit (index, row, audit) {
        // 
        if (audit === 2) {
          this.$prompt('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            inputPattern: /^[\s\S]{1,50}$/,
            inputErrorMessage: '50'
          }).then(({ value }) => {
            this.$message({
              type: 'success',
              message: ': ' + value
            })
            this.$axios.post(SITEURL.BANKCARD_AUDIT, {id: row.id, applyStatus: audit, remark: value}).then(response => {
              if (response.data.success) {
                this.$message.success('' + response.data.message)
                this.loading = true
                this.getBankInfo()
                this.tableData[index].applyStatus = audit
              } else {
                this.$message.error('' + response.data.message)
              }
            }).catch(error => {
              console.log(error)
            })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            })
          })
        } else {
          // 
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.$message({
              type: 'success',
              message: '!'
            })
            // 
            this.$axios.post(SITEURL.BANKCARD_AUDIT, {id: row.id, applyStatus: audit, remark: ''}).then(response => {
              if (response.data.success) {
                this.$message.success('' + response.data.message)
                this.loading = true
                this.getBankInfo()
                this.tableData[index].applyStatus = audit
              } else {
                this.$message.error('' + response.data.message)
              }
            }).catch(error => {
              console.log(error)
            })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            })
          })
        }
      },
      handleSizeChange (val) {
        console.log(` ${val} `)
      },
      handleCurrentChange (val) {
        this.currentPage = val
        this.loading = true
        this.getBankInfo(val)
      },
      handlePictureCardPreview (event) {
        this.dialogImageUrl = event.target.src
        this.dialogVisible = true
      }
    }
  }
</script>

<style scoped>
  .el-form-item {
    margin-bottom: 0 !important;
  }
  .breadcrumb {
    margin: 20px 0 20px 0;
  }
  .search {
    padding: 20px;
    background: #d3dce6;
  }
  .withdrawlist {
    padding-top: 20px;
  }

  .inline-input {
    width: 220px;
  }

  .cover-img {
    padding-top: 8px;
    width: 100px;
    height: 60px;
  }
</style>



