<template>
  <div class="sidebar">
    <el-menu class="el-menu-vertical" @open="handleOpen" @close="handleClose" theme="dark" :default-openeds="openeds">

      <el-submenu index="1">
        <template slot="title"><i class="el-icon-star-on"></i></template>
        <el-menu-item-group>
          <router-link to="/member/list"><el-menu-item index="1-1" v-if="roleCheck('/member/list')"></el-menu-item></router-link>
          <router-link to="/member/disabled"><el-menu-item index="1-2" v-if="roleCheck('/member/disabled')"></el-menu-item></router-link>
          <router-link to="/member/superior/non"><el-menu-item index="1-3" v-if="roleCheck('/member/superior/non')"></el-menu-item></router-link>
        </el-menu-item-group>
      </el-submenu>

      <el-submenu index="2">
        <template slot="title"><i class="el-icon-caret-bottom"></i></template>
        <el-menu-item-group>
          <router-link to="/member/integral"><el-menu-item index="2-1" v-if="roleCheck('/member/integral')"></el-menu-item></router-link>
        </el-menu-item-group>
      </el-submenu>

      <el-submenu index="3">
        <template slot="title"><i class="el-icon-menu"></i></template>
        <el-menu-item-group>
          <router-link to="/member/bankcard/list"><el-menu-item index="3-1" v-if="roleCheck('/member/bankcard/list')"></el-menu-item></router-link>
          <router-link to="/member/bankcard/apply/list"><el-menu-item index="3-2" v-if="roleCheck('/member/bankcard/apply/list')"></el-menu-item></router-link>
        </el-menu-item-group>
      </el-submenu>

      <el-submenu index="4">
        <template slot="title"><i class="el-icon-edit"></i></template>
        <el-menu-item-group>
          <router-link to="/member/student/checking"><el-menu-item index="4-1" v-if="roleCheck('/member/student/checking')"></el-menu-item></router-link>
          <router-link to="/member/student/pass"><el-menu-item index="4-2" v-if="roleCheck('/member/student/pass')"></el-menu-item></router-link>
        </el-menu-item-group>
      </el-submenu>

    </el-menu>
  </div>
</template>

<script>
export default {
  name: 'member-sidebar',
  data () {
    return {
      openeds: ['1', '2', '3', '4']
    }
  },
  methods: {
    handleOpen (key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath)
    },
    roleCheck (menu) {
      menu = ',' + menu + ','
      if (this.$store.state.role.indexOf('superadmin') > 0) {
        return true
      }
      if (this.$store.state.menus.indexOf(menu) > 0) {
        return true
      }
      return false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



